<template>
<div class="notice">
   <div class="n-title fa">
       <p class="n-fp">{{articles.title}}</p>

       <div class="n-tp">
          <span class="left">{{articles.opendate}}</span>
          <div class="right" >
             <span class="left">点击数: <i>{{clickCount}}</i></span>
             <div class="left attention" :class="iscollect ? 'collect' : ''"  @click="gaincollect" >
                <i class="el-icon-plus"></i>{{collect}}
             </div>
          </div> 
         
       </div>
       <p class="n-thp">
         <span class="left" v-if="articles.oneName" >第一候选人：{{articles.oneName}}</span>
         <span v-else class="left">第一候选人:详见原文</span>
         <span class="right" v-if="articles.oneOffer">中标金额：{{articles.oneOffer}}万</span>
         <span class="right" v-else >中标金额：详见原文</span>
       </p>
   </div>
   <div class="n-detail fa">
      <div class="n-nav">
         <span class="left">原文</span>
         <span class="right n-color" @click="text" >访问原文出处》</span>
      </div>
      <div class="n-text" v-html="articles.content" >

      </div>
   </div>
</div>
</template>
<script>
import axios from 'axios'
import { getJsonData,collectionNotice,nocollectionNotice } from '@/api/index'
export default {
  data () {
    return {
      id:'2003264',
      clickCount:0,
      articles:[],
      source:'hunan',
      collect:'关注',
      iscollect:false
    }
  },
  methods: {
    gainDetail() {
      let dataParam = JSON.stringify({
          type:'2',
          source:this.source
        });
        getJsonData( "/notice/detail/" + this.id , dataParam).then(res => {
            if(res.code == 1) {
               this.articles = res.data[0]
               this.clickCount = res.clickCount
               this.iscollect = res.data[0].collected
               if(this.iscollect) {
                 this.collect = '已关注'
               } else {
                 this.collect = '关注'
               }
            }
        });
    },
    text() {
       window.open(this.articles.url, "_blank")
    },
    gaincollect() {
      if(this.iscollect) {
        nocollectionNotice({noticeid:this.id,source:this.source}).then(res => {
          if(res.code = 1) {
            this.iscollect = false
            this.collect = '关注'
          }
        })
      } else {
        collectionNotice({noticeid:this.id,type:'2',source:this.source}).then(res => {
          if(res.code = 1) {
            this.iscollect = true
            this.collect = '已关注'
          }
        })
      }
    }
  },
  created () {
    this.id = this.$route.query.id;
    this.source = this.$route.query.source;
    this.gainDetail()
  },
  components: {
  }
}
</script>
<style lang="less" scoped>
.notice {
  width: 100%;
  padding-top: 86px;
  .fa {
    width: 1020px;
    margin: 0 auto;
    background-color: #fff;
  }
  .n-title {
    margin-top: 40px;
    box-sizing: border-box;
    padding: 32px 20px 20px;
    .n-fp {
       color: #333;
       font-size: 24px;
       font-weight: 500;
       margin-bottom: 17px;
       text-align: center;
    }
    .n-tp {
      width: 550px;
      color: #666;
      font-size: 18px;
      margin: 0 auto;
      overflow: hidden;
      i {
        color: #EC7522;
      }
       .attention {
         margin-left: 20px;
         width: 62px;
         line-height: 22px;
         border: 1px solid #FE6603;
         text-align: center;
         font-size: 14px;
         border-radius: 5px;
         color:#FE6603;
         cursor: pointer;
         i {
           font-size: 12px;
         }
       }
       .collect {
         color:#fff;
         background-color: #FE6603;
         i {
           color:#fff;
         }
       }

    }
    .n-thp {
      color: #666;
      font-size: 16px;
      overflow: hidden;
      margin-top: 20px;
    }
  }
  .n-detail {
    margin: 40px auto 210px;    
    .n-nav {
      height: 70px;
      line-height: 70px;
      padding: 0 20px ;
      border-bottom: 1px solid #f2f2f2;
      font-size: 20px;
      color:#333;
    }
    .n-color {
      color:#EC7522;
    }
  }
  .n-text {
    padding: 26px 20px 70px;
  }  
}
</style>



